@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

//   
@mixin active {
  &:active:not(.focus-visible) {
    @content;
  }
}
//  
@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible),
    &:focus:not(.focus-visible) {
      @content;
    }
  }
}
